export function getBackgroundLeftTopCornerImageConfig() {
  return {
    frame: 'bg/corner_effect.png',
    tint: 0x6d2293,
    rotation: Math.PI
  };
}

export function getBackgroundRightTopCornerImageConfig() {
  return {
    frame: 'bg/corner_effect.png',
    tint: 0x6d2293,
    rotation: -Math.PI / 2
  };
}

export function getBackgroundRightBottomCornerImageConfig() {
  return {
    frame: 'bg/corner_effect.png',
    tint: 0x6d2293,
    rotation: 0
  };
}

export function getBackgroundLeftBottomCornerImageConfig() {
  return {
    frame: 'bg/corner_effect.png',
    tint: 0x6d2293,
    rotation: Math.PI / 2
  };
}

export function getHexagonArrowImageConfig() {
  return {
    frame: 'game/arrow.png'
  };
}

export function getInnerHexagonImageConfig() {
  return {
    frame: 'game/hexagon.png',
    scale: { x: 0.86, y: 0.86 }
  };
}

export function getMetronomeImageConfig() {
  return {
    frame: 'game/metronome.png'
  };
}

export function getMetronomePendulumImageConfig() {
  return {
    frame: 'game/metronome_pendulum.png'
  };
}

export function getOrbImageConfig(frame) {
  return {
    frame: `game/${frame}.png`
  };
}

export function getTraceImageConfig(frame) {
  return {
    frame
  };
}
