import { lego, not } from '@armathai/lego';
import { lastLevelGuard } from '../../../guards/game/level/last-level-guard';
import { itemsToCtaCompleteCommand } from '../../ad/cta/items-to-cta-complete-command';
import { updateCurrentLevelCommand } from '../update-current-level-command';

export function onLevelWinCommand() {
  lego.command
    .guard(lastLevelGuard)
    .execute(itemsToCtaCompleteCommand)

    .guard(not(lastLevelGuard))
    .execute(updateCurrentLevelCommand);
}
