import { getOrbImageConfig } from '../../../configs/image-configs';
import { getOrbEmitter2Config } from '../../../configs/particles-configs';
import { ASSETS, OrbType } from '../../../constants';
import { getViewByProperty, makeEmitter, makeImage } from '../../../utils';
import { sample } from '../../../utils/array/sample';
import { Container } from '../../../utils/container';
import { removeEmitter } from '../../../utils/index';
import WinStar from './win-star';

const HighlightColors = ['0xdbcd35'];

class OrbParticleComponent extends Container {
  constructor() {
    super();

    const gameView = getViewByProperty('name', 'GameView');

    this._sparkleEmitter2 = makeEmitter(getOrbEmitter2Config(gameView));
  }

  update() {
    this._updatePositionEmitter(this._sparkleEmitter2, getOrbEmitter2Config());
  }

  _updatePositionEmitter(emitter, config) {
    const { key = ASSETS, config: emitterConfig } = config;
    emitter.emit(key, this.worldPosition.x, this.worldPosition.y, emitterConfig);
  }

  destroy() {
    removeEmitter(this._sparkleEmitter2);
    this._sparkleEmitter2 = null;
    super.destroy();
  }
}

export class OrbComponent extends Phaser.Sprite {
  constructor() {
    const { frame, radius } = OrbType[CI_API.Globals.PARAMS.which_orb];
    super(CI_API.game, 0, 0);

    this._radius = radius;

    this._build(frame);
  }

  get radius() {
    return this._radius;
  }

  update() {
    super.update();
    this._particle && this._particle.update();
  }

  getRotation() {
    return this._orb.rotation;
  }

  showWin() {
    this.game.make
      .tween(this._winStar)
      .to({ alpha: 1 }, 800, null, true)
      .onComplete.addOnce(() => {
        this.game.make.tween(this._winStar).to({ alpha: [0.7, 0.4, 0.7] }, 500, null, true, 0, -1);
      });

    this._winStar.play();
    this.game.make
      .tween(this._winStar.scale)
      .to(
        { x: this.scale.x - 0.3, y: this.scale.y - 0.3 },
        this.game.rnd.between(800, 1200),
        Phaser.Easing.Sinusoidal.In,
        true,
        0,
        -1,
        true
      );
  }

  setRotation(rotation) {
    this._orb.rotation = rotation;
  }

  show(force = false) {
    if (force) {
      this.visible = true;
      this.alpha = 1;
    } else {
      this.visible = true;
      this.game.add.tween(this).to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true);
    }
    this._addParticle();
  }

  hide(force = false) {
    if (force) {
      this.visible = false;
      this.alpha = 0;
    } else {
      this.game.add
        .tween(this)
        .to({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true)
        .onComplete.add(() => {
          this.visible = false;
        });
    }
    this._particle && (this._particle.destroy(), (this._particle = null));
  }

  _addParticle() {
    const particleComponent = new OrbParticleComponent();
    this.addChild((this._particle = particleComponent));
  }

  _build(frame) {
    this._buildOrb(frame);
    this._buildWinStar();
  }

  _buildWinStar() {
    this._winStar = new WinStar(sample(HighlightColors));
    this.addChild(this._winStar);
  }

  _buildOrb(frame) {
    const img = makeImage(getOrbImageConfig(frame));
    img.anchor.set(0.5);
    this.addChild((this._orb = img));
  }
}
