import { getCtaStandardPlayButtonConfig } from '../../configs/button-configs';
import { getCTAStandardGridConfig } from '../../configs/grid-configs';
import { getCtaStandardTitleTextConfig } from '../../configs/text-configs';
import { getGameBounds, makeText } from '../../utils';
import { CTAView } from './cta-view';

export class CTAStandard extends CTAView {
  constructor() {
    super();
    this.build();
  }

  show() {
    const { x, y, width, height } = getGameBounds();

    this._title.alpha = 1;
    this._playButton.alpha = 1;

    this.game.make
      .tween(this._title)
      .from({ x: x + width + this._title.width / 2 }, 500, Phaser.Easing.Back.Out, true, 300);
    this.game.make
      .tween(this._playButton)
      .from({ y: y + height + this._playButton.height / 2 }, 300, Phaser.Easing.Back.Out, true, 800);
  }

  getGridConfig() {
    return getCTAStandardGridConfig();
  }

  build() {
    super.build(this.getGridConfig(), 0.9);
    this._buildTitle();
    this._buildPlayButton();
  }

  _buildPlayButton() {
    const btn = this.buildPlayButton(getCtaStandardPlayButtonConfig());
    btn.alpha = 0;
    this.setChild('btn', (this._playButton = btn));
  }

  _buildTitle() {
    const title = makeText(getCtaStandardTitleTextConfig());
    title.alpha = 0;
    this.setChild('title', (this._title = title));
  }
}
