import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getBackgroundGridConfig } from '../../configs/grid-configs';
import {
  getBackgroundLeftBottomCornerImageConfig,
  getBackgroundLeftTopCornerImageConfig,
  getBackgroundRightBottomCornerImageConfig,
  getBackgroundRightTopCornerImageConfig
} from '../../configs/image-configs';
import { getSparkleEmitterConfig } from '../../configs/particles-configs';
import { ViewEvents } from '../../events/view-events';
import { makeEmitter, makeImage } from '../../utils';
import { removeEmitter } from '../../utils/index';

export class BackgroundView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    lego.event.on(ViewEvents.Game.Resize, this._onResize, this);
  }

  build(config) {
    super.build(config);
    this._buildCornerEffects();
    this._buildSparkleEffect();
  }

  get name() {
    return 'BackgroundView';
  }

  getGridConfig() {
    return getBackgroundGridConfig();
  }

  _onResize() {
    this._sparkleEmitter && this._removeSparkleEffect();
    this._buildSparkleEffect();
  }

  _buildCornerEffects() {
    const leftTop = makeImage(getBackgroundLeftTopCornerImageConfig());
    const rightTop = makeImage(getBackgroundRightTopCornerImageConfig());
    const rightBottom = makeImage(getBackgroundRightBottomCornerImageConfig());
    const leftBottom = makeImage(getBackgroundLeftBottomCornerImageConfig());
    this.setChild('left-top', leftTop);
    this.setChild('right-top', rightTop);
    this.setChild('right-bottom', rightBottom);
    this.setChild('left-bottom', leftBottom);
  }

  _buildSparkleEffect() {
    this._sparkleEmitter = makeEmitter(getSparkleEmitterConfig(this));
  }

  _removeSparkleEffect() {
    removeEmitter(this._sparkleEmitter);
    this._sparkleEmitter = null;
  }
}
