import { COOLDOWN_TIME, LevelState } from '../../../constants';
import { delayRunnable, removeRunnable } from '../../../utils';
import { ObservableModel } from '../../observable-model';
import { OrbModel } from './orb-model';
import { TraceModel } from './trace-model';

export class LevelModel extends ObservableModel {
  constructor() {
    super('LevelModel');

    this._levelId = null;
    this._config = null;
    this._traces = null;
    this._orbs = null;
    this._activeTraceIndex = null;
    this._state = LevelState.Unknown;

    this.makeObservable('_state', '_activeTraceIndex');
  }

  get levelId() {
    return this._levelId;
  }

  get config() {
    return this._config;
  }

  get state() {
    return this._state;
  }

  get traces() {
    return this._traces;
  }

  get orbs() {
    return this._orbs;
  }

  get activeTraceIndex() {
    return this._activeTraceIndex;
  }

  // ACTIONS......
  show() {
    this._activeTraceIndex = 0;
    this._state = LevelState.Show;
  }

  hide() {
    this._state = LevelState.Hide;
  }

  showed() {
    this._state = LevelState.Idle;
    this._traces[this._activeTraceIndex].activate();
  }

  activateOrb() {
    this._state = LevelState.ActivateOrb;
  }

  orbActivated() {
    this._state = LevelState.Idle;
  }

  deactivateCurrentTrace() {
    this._traces[this._activeTraceIndex].deactivate();
  }

  activateNextTrace() {
    this._activeTraceIndex += 1;
    this._traces[this._activeTraceIndex].activate();
  }

  cooldown() {
    this._state = LevelState.Cooldown;

    this._traces.forEach(trace => {
      trace.idle();
    });

    this._winCheckRunnable = delayRunnable(COOLDOWN_TIME, this._cooldownTimerComplete, this);
  }

  levelLose() {
    removeRunnable(this._winCheckRunnable);
    this._state = LevelState.ResultLose;
  }

  restart() {
    this._activeTraceIndex = 0;
    this._traces.forEach(trace => {
      trace.restart();
    });

    this._traces[0].activate();
    this._state = LevelState.Restart;
  }

  restarted() {
    this._state = LevelState.Idle;
    this._traces[this._activeTraceIndex].activate();
  }

  initialize(levelConfig) {
    const { levelId, traces, orbs } = levelConfig;

    this._levelId = levelId;
    this._config = levelConfig;

    this._traces = this._initializeTraces(traces);
    this._orbs = this._initializeOrbs(orbs);
  }

  destroy() {
    removeRunnable(this._winCheckRunnable);
    super.destroy();
  }

  _cooldownTimerComplete() {
    this._state = LevelState.ResultWin;
  }

  _initializeTraces(tracesConfig) {
    const traces = [];

    tracesConfig.forEach(traceConfig => {
      const traceModel = new TraceModel(traceConfig);
      traces.push(traceModel);
    });

    return traces;
  }

  _initializeOrbs(orbsConfig) {
    const orbs = [];

    orbsConfig.forEach(orbConfig => {
      const orbModel = new OrbModel(orbConfig);
      orbs.push(orbModel);
    });

    return orbs;
  }
}
