import { Animations, getHighlightAnimationConfig } from '../../../configs/animation-configs';
import { makeAnimation } from '../../../utils';

export default class WinStar extends Phaser.Group {
  constructor(color) {
    super(CI_API.game);

    this._color = color;
    this._createView();
    this.alpha = 0;
  }

  _createView() {
    for (let i = 0; i < 4; i++) {
      const starRay = makeAnimation(getHighlightAnimationConfig(0, 0));
      starRay.anchor.set(0.5, 1);
      starRay.angle = 90 * i;
      starRay.tint = this._color;
      this.add(starRay);
    }
  }

  play() {
    this.children.forEach(child => {
      child.animations.play(Animations.Highlight.Idle);
    });
  }
}
