import { lego } from '@armathai/lego';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';

export class SoundObservant {
  constructor() {
    lego.event.on(ModelEvents.LoadModel.CompleteUpdate, this._onLoadCompleteUpdate, this);
    lego.event.once(ViewEvents.Game.UserInteraction, this._playGameTheme, this);
  }

  _onLoadCompleteUpdate(complete) {
    if (!complete) {
      return;
    }

    this._sound = soundLoader.addAudioSprite('audio');
  }

  _playGameTheme() {
    this._play('game-theme', 1);
  }

  _play(id, volume) {
    this._sound.play(id, volume);
  }

  _stop(id) {
    this._sound.stop(id);
  }
}
