import { ObservableModel } from '../observable-model';

export class PlayerModel extends ObservableModel {
  constructor() {
    super('PlayerModel');

    this._lives = CI_API.Globals.PARAMS.deaths_to_cta;
  }

  get lives() {
    return this._lives;
  }

  updateLives(lives) {
    this._lives = lives;
  }
}
