import { lego, not } from '@armathai/lego';
import { noLivesGuard } from '../../guards/player/no-lives-guard';
import { deathsToCtaCompleteCommand } from '../ad/cta/deaths-to-cta-complete-command';
import { levelRestartCommand } from '../game/level/level-restart-command';

export function onOrbsDropCompleteCommand() {
  lego.command
    .guard(not(noLivesGuard))
    .execute(levelRestartCommand)

    .guard(noLivesGuard)
    .execute(deathsToCtaCompleteCommand);
}
