import { lego } from '@armathai/lego';
import { LevelState, OrbType } from '../../../constants';
// import LayoutUtils from '../../../display/lu/layout-utils-general';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { getGameBounds, loopRunnable, removeRunnable } from '../../../utils';
import { Container } from '../../../utils/container';
import { MoveFunctions } from '../../../utils/game/move-functions';
import { OrbComponent } from './orb-component';
import { TraceView } from './trace-view';

export class LevelView extends Container {
  constructor(uuid, config) {
    super();

    this._uuid = uuid;
    this._config = config;
    this._levelId = config.levelId;
    this._traces = [];
    this._orbs = [];
    this._collidedOrbs = [];

    lego.event.on(ModelEvents.LevelModel.StateUpdate, this._onLevelStateUpdate, this);
    lego.event.on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisible, this);

    this._setLevelAngle();

    this._buildTraces();
    this._buildOrbs();
  }

  get name() {
    return 'LevelView';
  }

  get uuid() {
    return this._uuid;
  }

  get levelId() {
    return this._levelId;
  }

  rebuild() {
    this._setLevelAngle();
  }

  _setLevelAngle() {
    const { l, p } = this._config.levelAngle;
    this.angle = LP(l, p);
  }

  destroy() {
    this.game.tweens.removeFrom(this);
    super.destroy();
  }

  _onLevelStateUpdate(state, prevState, uuid) {
    if (this._uuid !== uuid) {
      return;
    }

    switch (state) {
      case LevelState.Show:
        this._show();
        break;
      case LevelState.Hide:
        this._hide().onComplete.add(() => {
          lego.event.emit(ViewEvents.LevelView.HideComplete, this._levelId);
        });
        break;
      case LevelState.Idle:
        break;
      case LevelState.Cooldown:
        this._startCheckCollision();
        break;
      case LevelState.ResultLose:
        this._dropOrbs();
        break;
      case LevelState.ResultWin:
        this._stopCheckCollision();
        this._startOrbsHighlight();
        break;
      case LevelState.ActivateOrb:
        this._activateOrb();
        lego.event.emit(ViewEvents.LevelView.OrbActivated);
        break;
      case LevelState.Restart:
        this._restartView();
        break;

      default:
        break;
    }
  }

  _onCtaVisible(value) {
    if (value && CI_API.Globals.PARAMS.cta_scrn_version === 'embedded') {
      this._hide().onComplete.add(() => {
        this.destroy();
      });
    }
  }

  _restartView() {
    this._collidedOrbs.length = 0;

    this._orbs.forEach((orbView, index) => {
      const { x, y } = this.toLocal(this._traces[index].getHexagonGlobalPosition(), null);

      orbView.position.set(x, y);
      orbView.hide(true);
    });

    lego.event.emit(ViewEvents.LevelView.RestartComplete, this._levelId);
  }

  _startCheckCollision() {
    this._collisionRunnable = loopRunnable(26, () => {
      for (let i = 0; i < this._orbs.length; i++) {
        for (let j = 0; j < this._orbs.length; j++) {
          if (this._orbs[i] === this._orbs[j]) {
            continue;
          }

          if (this._orbs[i].position.distance(this._orbs[j]) < OrbType[CI_API.Globals.PARAMS.which_orb].radius) {
            this._collidedOrbs.push(this._orbs[i], this._orbs[j]);
            this._stopCheckCollision();
            this._deactivateOrbs();
            lego.event.emit(ViewEvents.LevelView.OrbsCollisionStart);
            return;
          }
        }
      }
    });
  }

  _stopCheckCollision() {
    removeRunnable(this._collisionRunnable);
  }

  _startOrbsHighlight() {
    this._orbs.forEach(orb => {
      orb.showWin();
    });
  }

  _show() {
    const { x, width } = getGameBounds();

    this.game.add
      .tween(this)
      .from({ x: x + width + this.width / 2 }, 800, Phaser.Easing.Cubic.InOut, true)
      .onComplete.add(() => {
        lego.event.emit(ViewEvents.LevelView.ShowComplete, this._levelId);
      });
  }

  _hide() {
    const { x } = getGameBounds();

    return this.game.add.tween(this).to({ x: x - this.width }, 800, Phaser.Easing.Cubic.InOut, true);
  }

  _activateOrb() {
    const { activeTraceIndex, orbs } = store.game.currentLevel;
    const { moveFunction } = orbs[activeTraceIndex].config;

    const orbView = this._orbs[activeTraceIndex];
    const traceView = this._traces[activeTraceIndex];
    orbView.show();
    MoveFunctions[moveFunction](orbView, traceView);
  }

  _deactivateOrbs() {
    this._orbs.forEach(orb => {
      this.game.tweens.removeFrom(orb);
    });
  }

  _dropOrbs() {
    const { height } = getGameBounds();

    const unusedBalls = this._orbs.filter(orb => {
      for (let i = 0; i < this._collidedOrbs.length; i++) {
        if (orb === this._collidedOrbs[i]) return false;
      }

      return true;
    });

    unusedBalls.forEach(b => {
      this.game.make.tween(b).to(
        {
          y: this.angle === 0 ? height : b.y,
          x: this.angle === 0 ? b.x : height
        },
        1000,
        null,
        true
      );
    });

    const collidePoints = [
      new Phaser.Point(
        this._collidedOrbs[0].x - this._collidedOrbs[1].x,
        this._collidedOrbs[0].y - this._collidedOrbs[1].y
      ),
      new Phaser.Point(
        this._collidedOrbs[1].x - this._collidedOrbs[0].x,
        this._collidedOrbs[1].y - this._collidedOrbs[0].y
      )
    ];

    let lastDropTween;

    for (let i = 0; i < this._collidedOrbs.length; i++) {
      const xArray = [
        this._collidedOrbs[i].x + collidePoints[i].x * 2,
        this._collidedOrbs[i].x + collidePoints[i].x * 3,
        this._collidedOrbs[i].x + collidePoints[i].x * 5
      ];
      const yArray = [this._collidedOrbs[i].y + collidePoints[i].y * 2];

      yArray.push(height);

      lastDropTween = this.game.make
        .tween(this._collidedOrbs[i])
        .to(
          {
            x: this.angle === 0 ? xArray : yArray,
            y: this.angle === 0 ? yArray : xArray
          },
          1000,
          null,
          true
        )
        .interpolation(Phaser.Math.bezierInterpolation);
    }

    lastDropTween.onComplete.add(() => {
      lego.event.emit(ViewEvents.LevelView.OrbsDropComplete);
    });
  }

  _buildTraces() {
    const { traces } = store.game.getLevelByUuid(this._uuid);

    traces.forEach(traceModel => {
      const { uuid, config } = traceModel;
      const { angle, x, y, hexagon } = config;

      const trace = new TraceView(uuid, hexagon, config);
      trace.angle = angle;
      trace.position.set(x, y);
      trace.deactivate(true);
      this._traces.push(trace);
      this.addChild(trace);
    });
  }

  _buildOrbs() {
    const { orbs } = store.game.getLevelByUuid(this._uuid);
    orbs.forEach((orb, index) => {
      const { x, y } = this.toLocal(this._traces[index].getHexagonGlobalPosition(), null);

      const orbView = new OrbComponent();
      orbView.position.set(x, y);
      orbView.hide(true);
      this._orbs.push(orbView);
      this.addChild(orbView);
    });
  }
}
