import { InputPriority } from '../constants';
import {
  getCtaEmbeddedPlayButtonPatchConfig,
  getCtaStandardPlayButtonPatchConfig,
  getCtaStyle1RetryButtonPatchConfig,
  getPersistentCtaButtonPatchConfig
} from './ninepatch-configs';
import {
  getCtaEmbeddedPlayButtonTextConfig,
  getCtaStandardPlayButtonTextConfig,
  getPersistentCtaTextConfig
} from './text-configs';

// PERSISTENT CTA
export function getPersistentCtaButtonConfig() {
  return {
    input: {
      name: 'persistent_cta_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getPersistentCtaButtonPatchConfig(),
        label: getPersistentCtaTextConfig()
      }
    }
  };
}

// CTA
export function getCtaStandardPlayButtonConfig() {
  return {
    input: {
      name: 'cta_play_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaStandardPlayButtonPatchConfig(),
        label: getCtaStandardPlayButtonTextConfig()
      }
    }
  };
}

