import { getHexagonArrowImageConfig, getInnerHexagonImageConfig } from '../../../configs/image-configs';
import { ASSETS } from '../../../constants';
import { makeImage } from '../../../utils';

export class HexagonComponent extends Phaser.Sprite {
  constructor(arrowConfig) {
    super(CI_API.game, 0, 0, ASSETS, `game/hexagon.png`);
    this.anchor.set(0.5);

    this._config = arrowConfig;

    this._build();
    this._startIdleAnimations();
  }

  show(force = false) {
    this.game.tweens.removeFrom(this);
    if (force) {
      this.visible = true;
      this.alpha = 1;
    } else {
      this.visible = true;
      this.game.add.tween(this).to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true);
    }
  }

  hide(force = false) {
    this.game.tweens.removeFrom(this);
    if (force) {
      this.visible = false;
      this.alpha = 0;
    } else {
      this.game.add
        .tween(this)
        .to({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true)
        .onComplete.addOnce(() => {
          this.visible = false;
        });
    }
  }

  _build() {
    this._buildArrow();
    this._buildInnerHexagon();
  }

  _buildArrow() {
    const { angle } = this._config;
    const arrow = makeImage(getHexagonArrowImageConfig());
    arrow.anchor.set(0.5, 1);
    arrow.y = -35;
    arrow.angle = angle;

    this.addChild((this._arrow = arrow));
  }

  _buildInnerHexagon() {
    const innerHexagon = makeImage(getInnerHexagonImageConfig());
    this.addChild((this._innerHexagon = innerHexagon));
  }

  _startIdleAnimations() {
    this._startArrowPulsation();
    this._startInnerHexagonPulsation();
  }

  _startArrowPulsation() {
    this.game.add
      .tween(this._arrow)
      .to({ alpha: 0.6 }, 400, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true).universal = true;
  }

  _startInnerHexagonPulsation() {
    this.game.add
      .tween(this._innerHexagon)
      .to({ alpha: 0.3 }, 400, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true).universal = true;
  }
}
