import { lego } from '@armathai/lego';
import { getTraceImageConfig } from '../../../configs/image-configs';
import { TraceState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';
import { HexagonComponent } from './hexagon-component';

export class TraceView extends Container {
  constructor(uuid, hexagonConfig, config) {
    super();

    this._uuid = uuid;
    this._config = config;
    const { framePassive, frameActive } = this.config;

    lego.event.on(ModelEvents.TraceModel.StateUpdate, this._onStateUpdate, this);

    this._build(framePassive, frameActive, hexagonConfig);
  }

  get uuid() {
    return this._uuid;
  }

  get config() {
    return this._config;
  }

  getHexagonGlobalPosition() {
    return this.toGlobal(this._hexagon.position);
  }

  activate() {
    this.alpha = 1;
    this._passive.visible = false;
    this._active.visible = true;
    this._hexagon.show();
  }

  deactivate(force = false) {
    this.alpha = 0.5;
    this._passive.visible = true;
    this._active.visible = false;
    this._hexagon.hide(force);
  }

  idle() {
    this.alpha = 1;
    this._passive.visible = false;
    this._active.visible = true;
  }

  _onStateUpdate(state, oldState, uuid) {
    if (uuid !== this._uuid) {
      return;
    }

    switch (state) {
      case TraceState.Active:
        this.activate();
        break;
      case TraceState.Passive:
        this.deactivate();
        break;
      case TraceState.Idle:
        this.idle();
        break;

      default:
        break;
    }
  }

  _build(framePassive, frameActive, hexagonConfig) {
    this._buildPassiveFrame(framePassive);
    this._buildActiveFrame(frameActive);
    this._buildHexagon(hexagonConfig);
  }

  _buildPassiveFrame(frame) {
    const img = makeImage(getTraceImageConfig(frame));
    this.addChild((this._passive = img));
  }

  _buildActiveFrame(frame) {
    const img = makeImage(getTraceImageConfig(frame));
    this.addChild((this._active = img));
  }

  _buildHexagon(config) {
    const { x, y, angle, arrow } = config;

    const hexagon = new HexagonComponent(arrow);
    hexagon.position.set(x, y);
    hexagon.angle = angle;
    this.addChild((this._hexagon = hexagon));
  }
}
