export const MoveFunctions = {
  _makeSinTween: function(target, xs, ys) {
    const twn = CI_API.game.make.tween(target).to({ x: xs, y: ys }, 1500, null, true);
    twn.universal = true;
    twn.onComplete.addOnce(() => {
      xs.reverse();
      ys.reverse();
      this._makeSinTween(target, xs, ys);
    });
    twn.onUpdateCallback(tw => {
      this._updateTargetAngle(target, tw);
    });
  },

  _updateTargetAngle: function(target, tween) {
    const {
      previousPosition: { x: px, y: py },
      position
    } = tween.target;
    const localPos = target.parent.toLocal({ x: px, y: py }, null);
    const angle = Phaser.Math.angleBetweenPoints(position, localPos);

    target.setRotation(angle - Math.PI / 2);
  },

  leftSinusoidalMove: function(target) {
    const xs = [];
    const ys = [];

    for (let i = 0, l = 50; i <= l; i++) {
      const r = ((Math.PI * 2) / l) * i;
      xs.push(Math.cos(r) * -110);
      ys.push((-684 / l) * i + 684 / 2);
    }

    this._makeSinTween(target, xs, ys);
  },

  rightSinusoidalMove: function(target) {
    const xs = [];
    const ys = [];

    for (let i = 0, l = 50; i <= l; i++) {
      const r = ((Math.PI * 2) / l) * i;
      xs.push(Math.cos(r) * 110);
      ys.push((684 / l) * i - 684 / 2);
    }

    this._makeSinTween(target, xs, ys);
  },

  leftCircleMove: function(target, shape) {
    target._angle = 0;

    const twn = CI_API.game.make.tween(target).to({ _angle: Math.PI * 2 }, 1500, null, true, 0, -1);

    twn.universal = true;

    twn.onUpdateCallback(() => {
      target.setRotation(target._angle);
      target.x = shape.x - Math.cos(target._angle) * shape.config.radius;
      target.y = shape.y - Math.sin(target._angle) * shape.config.radius;
    });
  },

  rightCircleMove: function(target, shape) {
    target._angle = 0;

    const twn = CI_API.game.make.tween(target).to({ _angle: Math.PI * 2 }, 1500, null, true, 0, -1);

    twn.universal = true;

    twn.onUpdateCallback(() => {
      target.setRotation(target._angle - Math.PI);
      target.x = shape.x + Math.cos(target._angle) * shape.config.radius;
      target.y = shape.y + Math.sin(target._angle) * shape.config.radius;
    });
  },

  rightLineMove: function(target) {
    target.setRotation(Math.PI / 2);

    const twn = CI_API.game.make.tween(target).to({ x: 430 }, 1500, null, true, 0, -1, true);

    twn.universal = true;

    twn.onLoop.add(() => {
      target.setRotation(target.getRotation() + Math.PI);
    });
  },

  downLineMove: function(target) {
    target.setRotation(Math.PI);

    const twn = CI_API.game.make.tween(target).to({ y: 285 }, 1500, null, true, 0, -1, true);

    twn.universal = true;

    twn.onLoop.add(() => {
      target.setRotation(target.getRotation() + Math.PI);
    });
  }
};
