import { LOOT_BOX_TIME } from '../constants';

export function getTutorialConfig() {
  const params = CI_API.Globals.PARAMS;

  return [
    { text: 'YOU GOT A NEW ORB! TRY IT OUT!', duration: LOOT_BOX_TIME, clickable: true },
    { text: "DON'T LET THE ORBS TOUCH!", duration: params.tutorial_time, clickable: true }
  ];
}
