import { TraceState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class TraceModel extends ObservableModel {
  constructor(config) {
    super('TraceModel');

    this._config = config;
    this._state = TraceState.Unknown;

    this.makeObservable('_state');
  }

  get state() {
    return this._state;
  }

  get config() {
    return this._config;
  }

  // ACTIONS
  activate() {
    this._state = TraceState.Active;
  }

  deactivate() {
    this._state = TraceState.Passive;
  }

  idle() {
    this._state = TraceState.Idle;
  }

  restart() {
    this._state = TraceState.Passive;
  }
}
