export const Spines = {
  IntroLootBox: {
    Key: 'intro_loot_box',
    Animations: {
      Intro: 'intro',
      IntroIdle: 'intro_idle'
    },
    Skins: {
      bee: 'orb',
      eye: 'orb_1',
      record: 'orb_2'
    }
  },
  CTALootBox: {
    Key: 'cta_loot_box',
    Animations: {
      CTA: 'cta',
      CTAIdle: 'cta_idle'
    }
  }
};

export function getIntroLootBoxSpineConfig(x, y) {
  return {
    key: Spines.IntroLootBox.Key,
    x,
    y
  };
}

export function getCTALootBoxSpineConfig(x, y) {
  return {
    key: Spines.CTALootBox.Key,
    x,
    y
  };
}
