import { ASSETS } from '../constants';

export const Animations = {
  Hand: {
    Click: 'click'
  },
  Highlight: {
    Idle: 'idle'
  }
};

export function getHandAnimationConfig(x, y) {
  return {
    key: ASSETS,
    x,
    y,
    data: [
      {
        name: Animations.Hand.Click,
        prefix: 'animations/hand/hand_',
        start: 1,
        stop: 3,
        suffix: '.png',
        zeroPad: 2,
        frameRate: 2,
        loop: true
      }
    ]
  };
}

export function getHighlightAnimationConfig(x, y) {
  return {
    key: ASSETS,
    x,
    y,
    data: [
      {
        name: Animations.Highlight.Idle,
        prefix: 'animations/highlight/fx_',
        start: 0,
        stop: 34,
        suffix: '.png',
        zeroPad: 4,
        frameRate: 60,
        loop: true
      }
    ]
  };
}
