import { lego } from '@armathai/lego';
import { AdViewState, GameOverReasons } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

export class WrapperObservant {
  constructor() {
    lego.event
      .on(ModelEvents.LoadModel.ProgressUpdate, this._onLoadProgress, this)
      .on(ModelEvents.LoadModel.CompleteUpdate, this._onLoadComplete, this)
      .on(ModelEvents.AdModel.ViewStateUpdate, this._onViewStateUpdate, this)
      .on(ViewEvents.PersistentCtaView.Click, this._clickGo, this)
      .on(ViewEvents.CtaView.ScreenClick, this._clickGo, this)
      .on(ViewEvents.CtaView.PlayClick, this._clickGo, this)
      .on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisibleUpdate, this)
      .on(ViewEvents.Game.UserInteraction, this._markInteraction, this);
  }

  _onLoadProgress(progress) {
    wrapper_load_progress(progress);
  }

  _onLoadComplete() {
    wrapper_preload_complete();
  }

  _onViewStateUpdate(state) {
    state === AdViewState.Create && this._hideSplash();
  }

  _hideSplash() {
    wrapper_hide_splash();
  }

  _clickGo() {
    wrapper_click_go();
  }

  _markInteraction() {
    wrapper_mark_interaction();
  }

  _onCtaVisibleUpdate(visible) {
    if (!visible) {
      return;
    }

    if (store.ad.cta.reason === GameOverReasons.Unknown) {
      console.error('Please provide game over reason');
    }

    wrapper_mark_cta(store.ad.cta.reason);
  }
}
