import { lego } from '@armathai/lego';
import { Animations, getHandAnimationConfig } from '../../configs/animation-configs';
import { GameState, LevelState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getGameBounds, makeAnimation } from '../../utils';
import { Container } from '../../utils/container';

export class HintView extends Container {
  constructor() {
    super();

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    this._build();

    this._hide();
  }

  destroy() {
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _build() {
    const handAnimation = makeAnimation(getHandAnimationConfig(0, 0));
    this.addChild((this._animation = handAnimation));
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    const { width, height } = getGameBounds();

    if (store.game.state === GameState.ShowLoot) {
      this._startHint(new Phaser.Point(...LP([width * 0.38, height * 0.65], [width * 0.3, height * 0.55])));
      return;
    }

    if (!store.game.currentLevel) {
      lego.event.emit(ViewEvents.HintView.NoHintPlace);
      return;
    }

    const { state } = store.game.currentLevel;
    if (state !== LevelState.Idle) {
      lego.event.emit(ViewEvents.HintView.NoHintPlace);
      return;
    }

    this._startHint(new Phaser.Point(width * 0.1, height * 0.2));
  }

  _hide() {
    this.game.tweens.removeFrom(this.scale);
    this.visible = false;
  }

  _startHint(pos) {
    this.position.set(pos.x, pos.y);
    this._animation.play(Animations.Hand.Click);

    this.game.tweens.removeFrom(this);
    this.alpha = 1;
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true);
    this.visible = true;
  }
}
