import { ASSETS } from '../constants';
import { getGameBounds } from '../utils';

const sparkleParticle = 'sparkleParticle';
const orbParticle2 = 'orbParticle2';

function getSparkleParticlesConfig() {
  return {
    key: sparkleParticle,
    data: {
      lifespan: 25000,
      image: ASSETS,
      alpha: {
        initial: 0,
        value: 0.1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.5, y: 0.9 },
          { x: 1, y: 0 }
        ]
      },
      frame: ['bg/bubble.png', 'bg/bubble_2.png'],
      scale: {
        initial: 0,
        value: 4,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.5, y: 0.9 },
          { x: 1, y: 0 }
        ]
      },
      vx: { min: -0.1, max: 0.1 },
      vy: { min: -0.1, max: 0.1 }
    }
  };
}

function getOrbParticles2Config() {
  return {
    key: orbParticle2,
    data: {
      lifespan: 500,
      image: ASSETS,
      frame: [
        'particles/blue_1.png',
        'particles/blue_2.png',
        'particles/purple_big.png',
        'particles/purple_blur.png',
        'particles/red.png',
        'particles/white.png',
        'particles/yellow_2.png',
        'particles/yellow.png'
      ],
      alpha: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.3 },
          { x: 0.3, y: 0.99 },
          { x: 0.99, y: 1 }
        ]
      },
      scale: {
        initial: 0,
        value: 0.3,
        control: [
          { x: 0, y: 0.2 },
          { x: 0.2, y: 0.9 },
          { x: 1, y: 0 }
        ]
      },
      vx: { min: -2, max: 2 },
      vy: { min: -2, max: 2 }
    }
  };
}

export const particles = [getSparkleParticlesConfig(), getOrbParticles2Config()];

export function getSparkleEmitterConfig(parent) {
  const { width, height } = getGameBounds();
  return {
    key: sparkleParticle,
    parent,
    // force: { x: 0, y: 0.15 },
    config: {
      zone: CI_API.game.particleStorm.createRectangleZone(width, height),
      repeat: -1,
      frequency: 3000
    }
  };
}

export function getOrbEmitter2Config(parent) {
  return {
    key: orbParticle2,
    parent,
    config: {
      total: 1
    }
  };
}
