import { lego } from '@armathai/lego';
import { levelResultLoseStateGuard } from '../../../guards/game/level/level-result-lose-state-guard';
import { levelResultWinStateGuard } from '../../../guards/game/level/level-result-win-state-guard';
import { onLevelLoseCommand } from './on-level-lose-command';
import { onLevelWinCommand } from './on-level-win-command';

export function onLevelStateUpdateCommand(state) {
  lego.command
    .guard(levelResultWinStateGuard)
    .execute(onLevelWinCommand)

    .guard(levelResultLoseStateGuard)
    .execute(onLevelLoseCommand);
}
