import { FONT1 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text) {
  return {
    text,
    y: LP(3, 4),
    style: {
      font: FONT1,
      fontSize: 38,
      fill: '#ffffff',
      align: 'center',
      wordWrap: LP(false, true),
      wordWrapWidth: 330
    }
  };
}

// PERSISTENT
export function getPersistentCtaTextConfig() {
  return {
    text: 'cta_btn_persistent_text',
    y: 4,
    style: {
      font: FONT1,
      fontSize: 34,
      fill: '#ffffff',
      shadow: {
        x: 1,
        y: 2,
        color: '#000000',
        blur: 4,
        shadowFill: true
      }
    }
  };
}

