import { getGameConfig } from '../../configs/game-config';
import { GameState, LOOT_BOX_TIME } from '../../constants';
import { delayRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';
import { LevelModel } from './level/level-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._state = GameState.Unknown;
    this._levels = null;
    this._currentLevelId = undefined;

    this.makeObservable();
  }

  // GET,SET......
  get state() {
    return this._state;
  }

  get levels() {
    return this._levels;
  }

  get currentLevelId() {
    return this._currentLevelId;
  }

  get currentLevel() {
    return this.getLevelById(this._currentLevelId);
  }

  getLevelById(id) {
    return this.levels[id - 1];
  }

  getLevelByUuid(uuid) {
    return this.levels.find(level => level.uuid === uuid);
  }

  // ACTIONS.....
  showLootBox() {
    this._state = GameState.ShowLoot;

    this._lootBoxRunnable = delayRunnable(LOOT_BOX_TIME, () => {
      this.hideLootBox();
    });
  }

  hideLootBox() {
    removeRunnable(this._lootBoxRunnable);
    this._state = GameState.HideLoot;
    this.createLevel(1);
  }

  createLevel(levelId, delay = 0) {
    this._createLevelDelay = delayRunnable(delay, () => {
      this._currentLevelId = levelId;
      this._state = GameState.CreateLevel;

      const currentLevel = this.getLevelById(this._currentLevelId);
      const prevLevel = this.getLevelById(this._currentLevelId - 1);

      currentLevel.show();
      prevLevel && prevLevel.hide();
    });
  }

  levelCreated() {
    this._state = GameState.Idle;
  }

  levelDestroyed() {
    this._state = GameState.Idle;
  }

  destroyLevel() {
    this._state = GameState.DestroyLevel;
  }

  initialize() {
    const allConfig = getGameConfig();
    const levels = [];

    const config = allConfig.slice(0, CI_API.Globals.PARAMS.number_tracks);

    config.forEach(levelConfig => {
      const levelModel = new LevelModel();
      levelModel.initialize(levelConfig);

      levels.push(levelModel);
    });

    this._levels = levels;

    this.showLootBox();
  }

  destroy() {
    this._levels.forEach(level => {
      level.destroy();
    });
    this._levels = null;
    removeRunnable(this._lootBoxRunnable);
    removeRunnable(this._createLevelDelay);
    super.destroy();
  }
}
