import { lego, not } from '@armathai/lego';
import { lastTraceGuard } from '../../guards/game/level/last-trace-guard';
import { activateNextTraceCommand } from '../game/level/activate-next-trace-command';
import { activateOrbCommand } from '../game/level/activate-orb-command';
import { cooldownCommand } from '../game/level/cooldown-command';
import { deactivateCurrentTraceCommand } from '../game/level/deactivate-current-trace-command';

export function onScreenClickCommand() {
  lego.command
    .execute(activateOrbCommand, deactivateCurrentTraceCommand)

    .guard(lastTraceGuard)
    .execute(cooldownCommand)

    .guard(not(lastTraceGuard))
    .execute(activateNextTraceCommand);
}
