import { lego } from '@armathai/lego';
import { getLootBoxPopupBgPatchConfig } from '../../configs/ninepatch-configs';
import { getIntroLootBoxSpineConfig, Spines } from '../../configs/spine-configs';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { getGameBounds, makeNinePatch, makeSpine, postRunnable } from '../../utils';
import { Container } from '../../utils/container';

const getBoxScale = () => {
  return LP(0.65, 1);
};

const getBoxYRatio = () => {
  return 0.26;
};

export class LootBoxView extends Container {
  constructor(uuid, config) {
    super();
    this._uuid = uuid;

    lego.event.on(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this);
    lego.event.once(ViewEvents.Game.UserInteraction, this._onUserInteraction, this);
    this.onDestroyed = new Phaser.Signal();

    this._build();
  }

  get name() {
    return 'LootBoxView';
  }

  get uuid() {
    return this._uuid;
  }

  destroy() {
    this.game.tweens.removeFrom(this);
    super.destroy();
  }

  rebuild() {
    const { width, height } = getLootBoxPopupBgPatchConfig();
    this._bg.resize(width, height);
    this._box.visible = false;
    postRunnable(() => {
      this._box.visible = true;
      this._updateBoxTransform();
    });
  }

  _onCtaPreVisibleUpdate(value) {
    if (value) {
      this.hide();
    }
  }

  _onUserInteraction() {
    lego.event.emit(ViewEvents.LootBoxView.Click);
  }

  hide() {
    if (this._hideTween) {
      return;
    }

    const { x } = getGameBounds();
    this.onDestroyed.dispatch();
    this._hideTween = this.game.add
      .tween(this)
      .to({ x: x - this.width / 2 }, 800, Phaser.Easing.Cubic.InOut, true)
      .onComplete.add(() => {
        this.destroy();
      });
  }

  _build() {
    this.add((this._bg = this._buildBg()));
    this.add((this._box = this._buildBox()));
    this._box.setToSetupPose();
    this._box.setSkinByName(Spines.IntroLootBox.Skins[CI_API.Globals.PARAMS.which_orb]);
    this._updateBoxTransform();
    const intro = this._box.setAnimationByName(0, Spines.IntroLootBox.Animations.Intro, false);
    intro.onComplete = () => {
      this._box.setAnimationByName(0, Spines.IntroLootBox.Animations.IntroIdle, true);
    };
  }

  _buildBg() {
    return makeNinePatch(getLootBoxPopupBgPatchConfig());
  }

  _buildBox() {
    const spine = makeSpine(getIntroLootBoxSpineConfig());
    return spine;
  }

  _updateBoxTransform() {
    this._box.y = this._bg.height * getBoxYRatio();
    this._box.scale.set(getBoxScale());
  }
}
