import { getCtaEmbeddedPlayButtonConfig } from '../../configs/button-configs';
import { getCTAEmbeddedGridConfig } from '../../configs/grid-configs';
import { getCtaEmbeddedPopupBgPatchConfig } from '../../configs/ninepatch-configs';
import { getCTALootBoxSpineConfig, Spines } from '../../configs/spine-configs';
import { getCtaEmbeddedSubtitleTextConfig, getCtaEmbeddedTitleTextConfig } from '../../configs/text-configs';
import {
  getCtaEmbeddedPopupBgImageSize,
  getGameBounds,
  makeNinePatch,
  makeSpine,
  makeText,
  postRunnable
} from '../../utils';
import { CTAView } from './cta-view';

const getBoxScale = () => {
  return LP(0.65, 1);
};

const getBoxYRatio = () => {
  return 0.1;
};

class Popup extends Phaser.Group {
  constructor() {
    super(CI_API.game);
    this.add((this._bg = this._buildBg()));
    this.add((this._box = this._buildBox()));
    this._updateBoxTransform();
  }

  rebuild() {
    const { width, height } = getCtaEmbeddedPopupBgImageSize();
    this._bg.resize(width, height);
    this._box.visible = false;
    postRunnable(() => {
      this._box.visible = true;
      this._updateBoxTransform();
    });
    this._title && this._title.destroy();
    this.add((this._title = this._buildTitle()));
    this._subtitle && this._subtitle.destroy();
    this.add((this._subtitle = this._buildSubtitle()));
  }

  _buildBg() {
    const bg = makeNinePatch(getCtaEmbeddedPopupBgPatchConfig());
    return bg;
  }

  _buildBox() {
    const spine = makeSpine(getCTALootBoxSpineConfig());
    spine.setToSetupPose();
    const cta = spine.setAnimationByName(0, Spines.CTALootBox.Animations.CTA, false);
    cta.onComplete = () => {
      spine.setAnimationByName(0, Spines.CTALootBox.Animations.CTAIdle, true);
    };
    return spine;
  }

  _buildTitle() {
    const title = makeText(getCtaEmbeddedTitleTextConfig());
    title.anchor.set(0.5);
    title.y = this._bg.height * LP(0.35, 0.33);
    return title;
  }

  _buildSubtitle() {
    const subtitle = makeText(getCtaEmbeddedSubtitleTextConfig());
    subtitle.anchor.set(0.5);
    subtitle.y = this._title.y + subtitle.height * LP(0.9, 1.2);
    return subtitle;
  }

  _updateBoxTransform() {
    this._box.y = this._bg.height * getBoxYRatio();
    this._box.scale.set(getBoxScale());
  }
}

export class CTAEmbedded extends CTAView {
  constructor() {
    super();
    this.build();
  }

  getGridConfig() {
    return getCTAEmbeddedGridConfig();
  }

  build() {
    super.build(this.getGridConfig(), 0);
    this._buildPopup();
    this.setChild('btn', this.buildPlayButton(getCtaEmbeddedPlayButtonConfig()));
  }

  show() {
    const { x, width } = getGameBounds();
    this.game.add.tween(this).from({ x: x + width + this.width / 2 }, 800, Phaser.Easing.Cubic.InOut, true);
  }

  rebuild(config) {
    // this._tutorialView && this._tutorialView.rebuild();
    this._popup.rebuild();
    super.rebuild(config);
  }

  _buildPopup() {
    this.setChild('popup', (this._popup = new Popup()));
  }
}
