import { getMetronomeImageConfig, getMetronomePendulumImageConfig } from '../../../configs/image-configs';
import { getMetronomeTextConfig } from '../../../configs/text-configs';
import { COOLDOWN_TIME } from '../../../constants';
import { loopRunnable, makeImage, makeText, removeRunnable } from '../../../utils';
import { Container } from '../../../utils/container';

export default class MetronomeComponent extends Container {
  constructor() {
    super();
    this._pendulum = null;

    this._build();

    this.hide(true);
  }

  show() {
    this._resetView();

    this.game.add.tween(this).to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true);
    this._startTickAnimation();
  }

  hide(force = false) {
    if (force) {
      this.alpha = 0;
      this._resetView();
    } else {
      this.game.add
        .tween(this)
        .to({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true)
        .onComplete.add(() => {
          this._resetView();
        });
    }
  }

  _startTickAnimation() {
    this.game.make.tween(this._pendulum).to({ angle: [-15, 0, 15, 0] }, 1000, null, true, 0, -1).universal = true;

    let time = COOLDOWN_TIME / 1000;

    this._cooldownRunnable = loopRunnable(1000, () => {
      time = Math.max(time - 1, 0);
      this._updateText(`${time}`);
    });
  }

  _resetView() {
    this.game.tweens.removeFrom(this._pendulum);
    this._updateText(`${COOLDOWN_TIME / 1000}`);
    this._pendulum.angle = 0;
    removeRunnable(this._cooldownRunnable);
  }

  _updateText(text) {
    this._countdownTimeText.setText({
      text: text,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    });
  }

  _build() {
    this._buildTicker();
    this._buildMetronomePendulum();
    this._buildLabel();
  }

  _buildTicker() {
    const ticker = makeImage(getMetronomeImageConfig());
    ticker.anchor.set(0.5);
    this.add(ticker);
  }

  _buildMetronomePendulum() {
    const pendulum = makeImage(getMetronomePendulumImageConfig());
    pendulum.anchor.set(0.5, 1);
    pendulum.position.set(0, 17);
    this.add((this._pendulum = pendulum));
  }

  _buildLabel() {
    const label = makeText(getMetronomeTextConfig());
    label.position.set(0, 50);
    label.anchor.set(0.5);
    this.add((this._countdownTimeText = label));
  }
}
