import { CellAlign } from '@armathai/phaser2-grid';

export function getBackgroundGridLandscapeConfig() {
  return {
    // debug: { color: 0xd95027 },
    name: 'background',
    cells: [
      {
        name: 'left-top',
        bounds: { x: 0, y: 0, width: 0.5, height: 0.5 },
        align: CellAlign.LeftTop
      },
      {
        name: 'left-bottom',
        bounds: { x: 0, y: 0.5, width: 0.5, height: 0.5 },
        align: CellAlign.LeftBottom
      },
      {
        name: 'right-top',
        bounds: { x: 0.5, y: 0, width: 0.5, height: 0.5 },
        align: CellAlign.RightTop
      },
      {
        name: 'right-bottom',
        bounds: { x: 0.5, y: 0.5, width: 0.5, height: 0.5 },
        align: CellAlign.RightBottom
      }
    ]
  };
}

