import { lego } from '@armathai/lego';
import { Spines } from '../configs/spine-configs';
import LU from '../display/layout-utils';
import { ViewEvents } from '../events/view-events';
import '../kernel/atlas-rescale';
import '../kernel/multiple-atlas';
import { store } from '../models/store';

export class PreloaderState extends Phaser.State {
  init() {
    if (this.game && this.game.device && this.game.device.touch) {
      this.game.input.mouse.stop();
    }
    // this.stage.backgroundColor = '#010101';
    this.stage.disableVisibilityChange = true;

    const parentDiv = document.getElementById(LU.CONTAINER_NAME);
    parentDiv.style.backgroundImage = 'radial-gradient(#3d0269, #1e0a3b)';

    LU.init(this.game, true);
  }

  /**
   * Required function, please do not delete <br>
   * Enable handlers and use imageLoader to preload assets
   *
   * @name preload
   * @function
   * @returns {void}
   */
  preload() {
    utils.enableHandlers(this.game);

    imageLoader.registerGame(this.game);
    /**
     * Loading assets and modules due to xp-assets.json file params
     */
    xp.loadAssets({
      spine: [Spines.IntroLootBox.Key, Spines.CTALootBox.Key]
    });
    if (CI_API.Globals.PARAMS.sound) {
      this.preloadAudio();
    }
  }

  preloadAudio() {
    soundLoader.registerGame(this.game);
    soundLoader.loadAudioSprite(
      'audio',
      `${CI_API.Globals.WEB_ROOT}/audio/audio.mp3`,
      `${CI_API.Globals.WEB_ROOT}/embed/audio.json`
    );
  }

  /**
   * Required function, please do not delete <br>
   *
   * @name create
   * @function
   * @returns {void}
   */
  create() {
    store.ad.load.complete = true;
  }

  /**
   * @name loadUpdate
   * @function
   * @returns {void}
   */
  loadUpdate() {
    store.ad.load.progress = this.game.load.progress;
  }

  /**
   * Required function, please do not delete <br>
   * ad_state being set to live is important for partnerships
   *
   * @name update
   * @function
   * @returns {void}
   */
  update() {
    if (ad_state === 'ready') {
      ad_state = 'live';

      lego.event.emit(ViewEvents.Ad.Live);
    }
  }
}
