import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { getGameBounds } from '../../utils';

export function getCTAStyle1GridLandscapeConfig() {
  return {
    name: 'cta',
    bounds: getGameBounds(),
    // debug: { color: 0xffffff },
    scale: CellScale.None
  };
}

export function getCTAStyle1GridPortraitConfig() {
  return {
    name: 'cta',
    bounds: getGameBounds(),
    // debug: { color: 0xffffff },
    scale: CellScale.None
  };
}

export function getCTAStandardGridPortraitConfig() {
  return {
    // debug: { color: 0xd95027 },
    name: 'cta_standard',
    cells: [
      {
        bounds: { x: 0, y: 0.5, width: 1, height: 0.5 },
        name: 'title',
        align: CellAlign.CenterTop,
        padding: { y: 0.22 }
      },
      {
        name: 'btn',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        padding: { height: 1 - 0.05 },
        align: CellAlign.CenterBottom
      },
      {
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        name: 'blocker',
        scale: CellScale.Fill
      }
    ]
  };
}

