import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { GameState, InputPriority, LevelState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getGameBounds, makePixel } from '../../utils';
import { LevelView } from './level/level-view';
import MetronomeComponent from './level/metronome-component';
import { LootBoxView } from './loot-box-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._currentLevelView = null;
    this._prevLevelView = null;

    lego.event.on(ModelEvents.GameModel.StateUpdate, this._onGameStateUpdate, this);
    lego.event.on(ModelEvents.LevelModel.StateUpdate, this._onLevelStateUpdate, this);
    lego.event.on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisibleUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  build(config) {
    super.build(config);
    this._buildMetronome();
    this._buildHitArea();
  }

  rebuild(config) {
    this._currentLevelView && this._currentLevelView.rebuild();
    this._lootBox && this._lootBox.rebuild();
    this._resizeHitArea();
    super.rebuild(config);
  }

  _onGameStateUpdate(state) {
    switch (state) {
      case GameState.ShowLoot:
        this._buildLootBox();
        break;
      case GameState.HideLoot:
        this._lootBox && this._lootBox.hide();
        break;
      case GameState.CreateLevel:
        this._createLevel();
        break;
      case GameState.Idle:
        //
        break;
      case GameState.DestroyLevel:
        this._destroyLevel();
        break;

      default:
        break;
    }
  }

  _onLevelStateUpdate(state) {
    this._removeInputListener();

    switch (state) {
      case LevelState.Cooldown:
        this._metronome.show();
        break;
      case LevelState.ResultLose:
      case LevelState.ResultWin:
      case LevelState.Restart:
        this._metronome.hide();
        break;
      case LevelState.Idle:
        this._addInputListener();
        break;

      default:
        break;
    }
  }

  _onCtaVisibleUpdate(value) {
    if (value) {
      this._metronome.hide();
    }
  }

  _addInputListener() {
    this._hitArea.events.onInputDown.add(this._onScreenClick, this);
  }

  _removeInputListener() {
    this._hitArea.events.onInputDown.remove(this._onScreenClick, this);
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.GameView.Click);
  }

  _resizeHitArea() {
    const { x, y, width, height } = getGameBounds();
    this._hitArea.position.set(x, y);
    this._hitArea.width = width;
    this._hitArea.height = height;
  }

  _buildHitArea() {
    const { x, y, width, height } = getGameBounds();
    this._hitArea = makePixel({ alpha: 0, x, y, width, height });
    this._hitArea.inputEnabled = true;
    this._hitArea.input.priorityID = InputPriority.Game;
    this._hitArea.name = 'screen';

    this.addChild((this._hitArea = this._hitArea));
  }

  _buildMetronome() {
    const metronome = new MetronomeComponent();
    this.setChild('metronome', (this._metronome = metronome));
  }

  _createLevel() {
    const { currentLevelId } = store.game;
    const levelModel = store.game.getLevelById(currentLevelId);

    this._prevLevelView = this._currentLevelView;
    this._currentLevelView = this._makeLevel(levelModel);

    lego.event.emit(ViewEvents.GameView.LevelCreated, currentLevelId);
  }

  _destroyLevel() {
    const { levelId } = this._prevLevelView;

    this._prevLevelView.destroy();
    this._prevLevelView = null;

    lego.event.emit(ViewEvents.GameView.LevelDestroyed, levelId);
  }

  _makeLevel(levelModel) {
    const { uuid, config } = levelModel;
    const level = new LevelView(uuid, config);
    this.setChild('level', level);

    return level;
  }

  _buildLootBox() {
    this.setChild('loot_box', (this._lootBox = new LootBoxView()));
    this._lootBox.onDestroyed.addOnce(() => {
      this._lootBox = null;
    });
  }
}
