import { getTutorialBgImageConfig } from '../../configs/ninepatch-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import { fitText, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class TutorialSequenceView extends Container {
  constructor(config) {
    super();
    this._config = config;
    this._showTween = null;
    this._hideTween = null;
    this.alpha = 0;

    this.rebuild();
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  show() {
    return (this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true));
  }

  hide() {
    return (this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true));
  }

  _buildBg() {
    return makeNinePatch(getTutorialBgImageConfig());
  }

  _buildLabel(text) {
    const { width, height } = this._bg;

    const label = makeText(getTutorialTextConfig(text));
    label.anchor.set(0.5);
    fitText(label, width * 0.9, height * 0.9);

    return label;
  }

  rebuild() {
    this._bg && this._bg.destroy();
    this._label && this._label.destroy();
    this.addChild((this._bg = this._buildBg()));
    this.addChild((this._label = this._buildLabel(this._config.text)));
  }
}
