import { lego } from '@armathai/lego';
import { AdStatus, AdViewState, GameOverReasons } from '../constants';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';
import { store } from '../models/store';

const toSnakeCase = str =>
  str &&
  str
    .match(/[A-Z]{2,}(?=[A-Z][a-z]+[0-9]*|\b)|[A-Z]?[a-z]+[0-9]*|[A-Z]|[0-9]+/g)
    .map(x => x.toLowerCase())
    .join('_');

export class AnalyticsObservant {
  constructor() {
    lego.event
      .on(ViewEvents.CtaView.ScreenClick, this._onCtaScreenClick, this)
      .on(ViewEvents.CtaView.PlayClick, this._onCtaPlayClick, this)
      .on(ViewEvents.PersistentCtaView.Click, this._onPersistentCtaClick, this)
      .on(ModelEvents.TutorialModel.CompleteUpdate, this._onTutorialCompleteUpdate, this)
      .on(ModelEvents.AdModel.ViewStateUpdate, this._onViewStateUpdate, this)
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this)
      .on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisibleUpdate, this);
  }

  _onCtaScreenClick() {
    analytics.logClick('cta_click', 'cta_overlay');
  }

  _onCtaPlayClick() {
    analytics.logClick('cta_click', 'cta_button');
  }

  _onPersistentCtaClick() {
    analytics.logClick('cta_click', 'persistent_cta_button');
  }

  _onTutorialCompleteUpdate(complete) {
    if (complete) {
      analytics.logProgress('tutorial', 'complete');
    }
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Restart:
        this._logCtaOverride();
        break;
      default:
    }
  }
  _onCtaVisibleUpdate(visible) {
    if (!visible) {
      return;
    }

    this._logCta();
  }

  _logCtaOverride() {
    analytics.logProgress('cta_override', 'restart');
  }

  _logCta() {
    switch (store.ad.cta.reason) {
      case GameOverReasons.Idled:
        this._logIdledCta();
        break;
      case GameOverReasons.ItemsToCtaReached:
        this._logItemsToCtaReached();
        break;
      case GameOverReasons.DeathsToCtaReached:
        this._logDeathsToCtaReached();
        break;
      default:
        throw new Error(`Unknown CTA reason`);
    }
  }

  _logIdledCta() {
    const { currentLevelId, levels } = store.game;
    analytics.logProgress('items_left', `${levels.length - (currentLevelId - 1)}`);
  }

  _logItemsToCtaReached() {
    analytics.logProgress('items_to_cta', '');
  }

  _logDeathsToCtaReached() {
    const { currentLevelId, levels } = store.game;
    analytics.logProgress('items_left', `${levels.length - (currentLevelId - 1)}`);
  }

  _onViewStateUpdate(state) {
    switch (state) {
      case AdViewState.Create:
        CI_API.game.input.onDown.addOnce(this._onGameDown, this);
        lego.event.off(ModelEvents.AdModel.ViewStateUpdate, this._onViewStateUpdate, this);
        break;
      default:
    }
  }

  _onGameDown(pointer) {
    if (!pointer.targetObject || !pointer.targetObject.sprite) {
      analytics.logClick('first_interaction_click', 'unknown');
      return;
    }
    analytics.logClick('first_interaction_click', toSnakeCase(pointer.targetObject.sprite.name));
  }
}
