import { fitText, makeImage, makeNinePatch, makeText } from '..';
import { InputHandler } from './input-handler';

class AbstractButton extends InputHandler {
  constructor({ states, input }) {
    super();

    this.onClick = new Phaser.Signal();
    this.states = this.createStates(states);
    this.inputArea = this.createHitArea(input);

    this.switchEnable(true);
  }

  switchEnable(value) {
    super.switchInput(value);

    this.setState('up');

    if (value === false) {
      this.setState('disable');
    }
  }

  // override
  onDown(target, pointer) {
    this.setState('down');
  }

  // override
  onUp(target, pointer, isOver) {
    this.setState('up');

    if (isOver) {
      this.onClick.dispatch(this);
    }
  }

  createStates({ up, down, disable } = {}) {
    return {
      up: up && this.createState(up),
      down: down && this.createState(down),
      disable: disable && this.createState(disable)
    };
  }

  // abstract
  createState(config) {
    throw new Error('createState abstract function is not implemented');
  }

  setState(key) {
    if (!this.states[key]) {
      return;
    }

    for (let prop in this.states) {
      if (this.states.hasOwnProperty(prop) && this.states[prop]) {
        this.states[prop].visible = false;
      }
    }

    this.states[key].visible = true;
  }
}

export class Button extends AbstractButton {
  createState(config) {
    const state = new Phaser.Group(this.game);

    const { bg, label } = config;

    // FRAME
    const bgObj = bg.width && bg.height ? makeNinePatch(bg) : makeImage(bg);
    state.add(bgObj);

    // LABEL
    if (label) {
      const labelObj = makeText(label);
      labelObj.anchor.set(0.5);
      state.add(labelObj);

      fitText(labelObj, bgObj.width * 0.9, bgObj.height * 0.9);
    }

    return this.add(state);
  }
}
